<template>
  <div class="login_body">
    <div>
      <input type="text" class="login_text" placeholder="账号/手机号/邮箱"  v-model="zhan"/>
    </div>
    <div>
      <input type="text" class="login_text" placeholder="请输入您的密码" v-model="mima"/>
    </div>
    <div class="login_btn">
      <input type="submit" value="登录"  @click="denglu()"/>
    </div>
    <div class="login_link">
      <a href="#">立即注册</a>
      <a href="#">找回密码</a>
    </div>
  </div>
</template>

<script>
export default {
    name:"Login",
    data() {
        return {
            zhan:'',
            mima:''
        }
    },
   methods: {
       denglu:function(){
               const self = this;
           if(this.zhan==""||this.mima==""){
               alert("账号/手机号/邮箱/密码不能为空！")
           } else if(!(/^1[34578]\d{9}$/.test(self.zhan))){
               alert("手机号格式不对")
           }else{
               alert("登录成功")
           }
       }
   },
}
</script>

<style  scoped>
*{
    margin: 0;
    padding: 0;
}
#content .login_body{
    width: 100%;
}
.login_body  .login_text{
    width: 100%;
    height: 40px;
    border: none;
    border-bottom: 1px solid #cccccc;
    margin: 0 5px;
    outline: none;
}
.login_body .login_btn{
    height: 50px;
    margin: 10px;
}
.login_body .login_btn input{
    display: block;
    width: 100%;
    height: 100%;
    background: #e54847;
    border: none;
    color: white;
}
.login_body .login_link{
    display: flex;
    justify-content: space-between;
}
.login_body  .login_link a{
    text-decoration: none;
    margin: 0 5px;
    font-size: 12px;
    color: #e54847;
}
</style>
